.footer {
    background-color: #50577A;
    color: white;
    padding: 1rem 0;
    text-align: center;
    /*min-height: 100vh;*/
}

.footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.social-icons {
    list-style: none;
    display: flex;
    gap: 1rem;
    margin-top: 0.5rem;
}

.social-icons li a {
    text-decoration: none;
    color: white;
    font-size: 1.25rem;
    transition: color 0.3s ease;
}

.social-icons li a:hover {
    color: lightgray;
}
