.header {
    background-color: #404258;
    color: white;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-size: 1.5rem;
    margin-left: 50px;
    font-family: "Courier New";
    color: white;
}

.nav-list {
    list-style: none;
    display: flex;
    gap: 1rem;
    margin-right: 5000px;
}

.nav-item a {
    text-decoration: none;
    color: white;
    transition: color 0.3s ease;
}

.nav-item a:hover {
    color: lightgray;
}

