.body {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    color: white;
    background-color: #474E68;
    min-height: calc(100vh - 40px);
}

.section {
    margin-bottom: 2rem;
}

.section h2 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.section p {
    margin-bottom: 1rem;
}

.section ul {
    list-style: disc;
    margin-left: 1.5rem;
}

.section a{
    color: #ffebcd;
}

.row {
    display: flex;
}

.column {
    flex: 50%;
}

.profile-pic {
    width: 25vw;
    height: auto;
    margin: auto;
    position: absolute;
    z-index: auto;
}